"use client";
import { useEffect, useState, useRef } from "react";
import { io, Socket } from "socket.io-client";
import { fetchMessages } from "@/services/api"; // Your API to fetch past messages

const BASE_URL = "http://localhost:5000";  // Should be defined at the top

function LoadingDots() {
  return <span className="animate-pulse text-sm text-gray-500">Typing...</span>;
}

interface Message {
  _id: string;
  content: string;
  sender: { _id: string; username: string } | null;
  createdAt: string;
  isAIResponse: boolean;
}

export default function ChatBox() {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [aiTyping, setAiTyping] = useState(false);
  const socketRef = useRef<Socket | null>(null);

  // Grab token from localStorage if available
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;
  const user_id:any = typeof window !== "undefined" ? localStorage.getItem("user_id") : null;
  useEffect(() => {
    if (!token) return;
    debugger;
    // Connect socket with token auth
    const socket = io("http://localhost:5000", {

      auth: { token },
    });
    socketRef.current = socket;

    // Fetch existing messages from API
  // fetchMessages(token, user_id).then((msgs) => setMessages(msgs));
    // Listen for new messages from server
    socket.on("newMessage", (msg: Message) => {

      setMessages((prev) => [...prev, msg]);
    });
    socket.on("connect_error", (err: any) => {
      console.error("âŒ Socket connection error:", err.message || err);
    });

    socket.on("aiTyping", (typing: boolean) => {
      setAiTyping(typing);
    });


    // Cleanup on unmount
    return () => {
      socket.disconnect();
      socketRef.current = null;
    };
  }, [token,user_id]);

  // Send message handler
  const handleSend = () => {
    if (!input.trim() || !socketRef.current) return;

    socketRef.current.emit("sendMessage", input.trim());
    setInput("");
  };

  return (
    <div className="p-4 h-screen flex flex-col">
      {/* Messages display */}
      <div className="flex-1 overflow-auto border mb-2 p-2 space-y-2">
        {messages.map((msg) => (
          <div
            key={msg._id}
            className={`p-2 rounded-md max-w-sm ${msg.sender && msg.sender.username
              ? "bg-blue-100 text-right ml-auto"
              : "bg-gray-100 text-left"
              }`}
          >
            {msg.content}
          </div>
        ))}
        {aiTyping && (
          <div className="p-2 bg-gray-200 rounded-md max-w-sm text-left">
            <LoadingDots />
          </div>
        )}
      </div>

      {/* Input box and send button */}
      <div className="flex">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          className="flex-1 border p-2"
          placeholder="Type your message..."
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              e.preventDefault();
              handleSend();
            }
          }}
        />
        <button
          onClick={handleSend}
          className="ml-2 bg-blue-600 text-white px-4 py-2"
        >
          Send
        </button>
      </div>
    </div>
  );
}
